import { createRouter, createMemoryHistory } from 'vue-router'
import ScreenOne from '../views/ScreenOne.vue'
import ScreenZero from '../views/ScreenZero.vue'

const router = createRouter({
  history: createMemoryHistory(),
  routes: [
    {
      path: '/',
      name: 'screen-zero',
      component: ScreenZero
    },
    {
      path: '/anim-two',
      name: 'anim-two',
      component: () => import('../views/AnimTwo.vue')
    },
    {
      path: '/anim-three',
      name: 'anim-three',
      component: () => import('../views/AnimThree.vue')
    },
    {
      path: '/anim-four',
      name: 'anim-four',
      component: () => import('../views/AnimFour.vue')
    },
    {
      path: '/screen-one',
      name: 'screen-one',
      component: ScreenOne
    },
    {
      path: '/screen-two',
      name: 'screen-two',
      component: () => import('../views/ScreenTwo.vue')
    },
    {
      path: '/screen-three',
      name: 'screen-three',
      component: () => import('../views/ScreenThree.vue')
    },
    {
      path: '/completion',
      name: 'completion',
      component: () => import('../views/CongratulationsPage.vue')
    }
  ]
})

export default router
